import React, { Suspense } from "react";
import { useSelector } from "react-redux";
import "./MM.css";
import { setMMmappingTempData, setMMmappingTempData2 } from "../../redux-toolkit/slice/action";
import NavBar from "../../commonComponents/Material/Navbar/NavBar";
import SideBarMenu from "../../commonComponents/SideBarMenu/SideBarMenu";
const Loading = React.lazy(() => import("../../commonComponents/Loading/Loading"));
const MaterialExport = React.lazy(() => import("../../commonComponents/Material/MaterialExport/MaterialExport"));
const MaterialGet = React.lazy(() => import("../../commonComponents/Material/MaterialGet/MaterialGet"));
const MaterialMap = React.lazy(() => import("../../commonComponents/Material/MaterialMap/MaterialMap"));

function MM() {
  const { materialAction, uploadLoader, MMmappingDataTemp } = useSelector((state) => state?.action);
  // const {} = useSelector((state) => state?.taskpane);
  return (
    <>
      <div className="commonContainer" id="commonContainer">
        <div className="commonNavBar">
          <NavBar />
        </div>
        <div className="commonBody ">
          <div className="commonSidebarBox">
            <SideBarMenu />
          </div>
          <div className="commonActivityArea">
            {materialAction === "get" ? (
              <Suspense fallback={<div></div>}>
                <MaterialGet />{" "}
              </Suspense>
            ) : null}
            {materialAction === "map" ? (
              <Suspense fallback={<div></div>}>
                <MaterialMap
                  mappingConfig={MMmappingDataTemp}
                  setmappingConfig={setMMmappingTempData}
                  setmappingConfig2={setMMmappingTempData2}
                />
              </Suspense>
            ) : null}
            {materialAction === "export" ? (
              <Suspense fallback={<div></div>}>
                {" "}
                <MaterialExport />{" "}
              </Suspense>
            ) : null}
            {materialAction === "update" ? (
              <Suspense fallback={<div></div>}>
                {" "}
                <MaterialGet />{" "}
              </Suspense>
            ) : null}
          </div>
        </div>
      </div>
      {uploadLoader && <Loading />}
    </>
  );
}

export default MM;
